import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS, Group, FormLayout, Input, Button, FormStatus } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import axios from 'axios'


const osName = platform();


const Start = ({ id, createUser }) => {
	const [formValue, setFormValue] = React.useState({group: "", num:""})
	const [formValid, setFormValid] = React.useState({group: "", num: ""})
	const [error, setError] = React.useState(null)

	const onChange = (e) => {
		const {name, value} = e.currentTarget
		if(!value){
			setFormValid(prev => ({...prev, group:{valid: "", text: ""}}))
		} else {
			if([name] == "group"){
				if(!/\b\d{6}\w?/.test(value)){
					setFormValid(prev => ({...prev, group: {valid: "error", text: "Номер группы состоит из 6 цифр и символов на конце"}}))
				} else {
					if(!/\b[1-9]\d{5}\b\w?/.test(value)){
						setFormValid(prev => ({...prev, group: {valid: "error", text: "Неверный формат номера группы"}}))
					} else {
						setFormValid(prev => ({...prev, group:{valid: "valid", text: "Идеально!"}}))
					}
				}
			}
			if([name] == "num"){
				if(!/\b\d{6}\b/.test(value)){
					setFormValid(prev => ({...prev, num: {valid: "error", text: "Номер зачётки состоит из 6 цифр"}}))
				} else {
					if(!/\b(1[3-9])|(2[01])\d{4}\b/.test(value)){
						setFormValid(prev => ({...prev, num: {valid: "error", text: "Неверный формат зачётки"}}))
					} else {
						setFormValid(prev => ({...prev, num:{valid: "valid", text: "Идеально!"}}))
					}
				}
			}
		}
		setFormValue((prev) => ({...prev, [name]: value}))
	}
	const submitForm = () => {
		if(formValid.group.valid == "valid" && formValid.num.valid == "valid"){
			createUser(setError, formValue)
		} else {
			if(formValid.group.valid != "valid"){
				setFormValid(prev => ({...prev, group: {valid: "error", text: "Введите пожалуйста номер группы"}}))
			}
			if(formValid.num.valid != "valid"){
				setFormValid(prev => ({...prev, num: {valid: "error", text: "Введите пожалуйста номер зачётки"}}))
			}
	}
	}
	return (<Panel id={id}>
		<PanelHeader>
			Помощник Антошки
		</PanelHeader>
		<FormLayout>
			{error && <FormStatus header={error.header} mode="error">{error.text}</FormStatus>}
			<Input 
				top="Номер группы"
				name="group" 
				status={formValid.group.valid || null} 
				bottom={formValid.group.text || null}
				onChange={onChange}
			/>
			<Input 
				top="Номер зачётки"
				name="num" 
				status={formValid.num.valid || null} 
				bottom={formValid.num.text || null}
				onChange={onChange}
				/>
			<Button size="l" onClick={submitForm}>Сохранить</Button>
		</FormLayout>
        <Group>
    </Group>
	</Panel>
);
}

Start.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Start;
